<template>
  <div id="scanQRCode">
    <b-container fluid>
      <div id="qr-reader" style="width: 400px;height: 400px;">
      <qrcode-stream @decode="onDecode"></qrcode-stream>
      </div>
    </b-container>
    Scan Result: {{scanResult}}
  </div>
</template>

<script>

//import Html5Qrcode from 'html5-qrcode';
//let Html5Qrcode = require('../../public/html5-qrcode.min.js');
//import '../../public/html5-qrcode.min.js';
import { QrcodeStream } from 'vue-qrcode-reader'
import qrLoginService from '../services/QRLoginService';

export default {
  name: "scanQRCode",
  components: {
    QrcodeStream,
  },
  data() {
    return {
      scanResult: "",
    };
  },

  mounted() {
  },

  methods: {
    onDecode (decodedString) {
      this.scanResult = decodedString;
      console.log(decodedString);

      let sessionid = this.scanResult.split("-")[0];
      qrLoginService.qrLogin(sessionid).then(resp => {
        console.log("session " + sessionid + " logged in as " + resp.data);
      });
    }
  },
};
</script>

<style>
body {
  font-family: Arial, Helvetica, sans-serif;
}
</style>
